<template>
  <main class="main">
    <Transitiongroup name="list">
      <div
        class="main__chatitem"
        v-for="(mess, inddex) in message"
        :key="inddex"
      >
        <div
          class="main__chatitem-body"
          :class="{
            send: mess.sendId == sendId,
            get: mess.sendId != sendId,
            photo: mess.photo?.length > 0 && mess.body.length == 0,
            'photo-text': mess.photo?.length > 0 && mess.body.length > 0.
          }"
        >
          <span class="main__chatitem-time">{{ mess.time }}</span>
          <div class="main__chatitem-text">
            <img :src="mess.photo" alt="" />
            <p class="main__chatitem-desc">{{ mess.body }}</p>
          </div>
        </div>
      </div>
    </Transitiongroup>
  </main>
</template>

<script>
export default {
  name: "WindowBody",
  props: {
    message: Array,
    sendId: Number,
  },
};
</script>
